.card[data-isFlipped="true"], .card[data-isDisabled="true"] {
  transform: rotateY(180deg);
}

.card {
  position: relative;
  transition: 0.3s;
  transform-style: preserve-3d;
}

.card .front,  .card .back{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-box-shadow: 0px 6px 5px 0px #864601;
  -moz-box-shadow: 0px 6px 5px 0px #864601;
  box-shadow: 0px 6px 5px 0px #864601;
  border: 1px solid #864601;
}

.card .front {
  background: #e7a61a;
  z-index: 2;
	/* for firefox 31 */
  transform: rotateY(0deg);
  font-size: 2rem;
  color: #864601;
}

.card .back {
  background: white;
  transform: rotateY(180deg);
}

.card[data-isDisabled="true"] .back {
  background:  #43B047;
  color:  #43B047;
}


